<template>
  <div v-if="content" class="read">
    <!-- 用于挂载的标签 -->
    <div class="my-container"></div>
    <div class="my-chapters"></div>
    <div class="my-controler"></div>
    <!-- 文章层 -->
    
    <!-- <div class="mask"></div> --> <!-- 亮度遮罩层 -->
    <van-cell @click="showPopup">
      <!-- :class="{ 'van-cell-nightcolor': isNight }" -->
      <template v-if="!content.isVip">
        <h2>{{ content.title }}</h2>
        <article class="read-article">
          <section
            class="read-section"
            ref="contentSection"
            @touchstart="readtouchstart"
            @touchmove="readtouchmove"
            @touchend="readtouchend"
          >
            <p
              v-for="(item, index) in content.cpContent.split('\n')"
              :key="index"
            >
              <!-- 去掉首尾空格 -->
              {{ item.replace(/(^\s*)|(\s*$)/g, "") }}
            </p>
          </section>
        </article>
        <div class="page">
          <span>{{ nowPage + 1 }}</span>
          <i>/</i>
          <span>{{ allPage + 1 }}</span>
        </div>
      </template>
      <template v-else>
        <h2>{{ content.title }}</h2>
        <p
          class="pMinHei"
          @touchstart="readtouchstart"
          @touchmove="readtouchmove"
          @touchend="readtouchend"
        >
          本章节是VIP章节，请升级到VIP再进行阅读
        </p>
      </template>
    </van-cell>

    <!-- 操作层上层 -->
    <van-popup
      v-model="show"
      position="top"
      :style="{ height: '40px' }"
      :get-container="getContainer"
    >
      <van-nav-bar
        :title="$route.query.bookName"
        left-arrow
        @click-left="Back"
      />
    </van-popup>
    <!-- 操作层下层 -->
    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '20%' }"
      :overlay="false"
      :get-container="getContainer"
    >
      <!-- 操作层章节控制部分 -->
      <div class="controlChapter">
        <span @click="prevChapter(value)">上一章</span>
        <van-slider
          v-model="value"
          :step="1"
          :max="temporaryChapters.length"
          button-size="15px"
        />
        <span @click="nextChapter(value)">下一章</span>
      </div>

      <!-- 操作层按钮部分 -->
      <van-tabbar v-model="active">
        <van-tabbar-item @click="showPopup_two">
          <img src="../../public/img/category.svg" />
          <p>目录</p>
        </van-tabbar-item>
        <van-tabbar-item
          @click="
            isNight = !isNight;
            show = false;
            controlBG(5);
          "
        >
          <img src="../../public/img/night.svg" v-if="isNight" />
          <img src="../../public/img/day.svg" v-else />
          <p>夜间</p>
        </van-tabbar-item>

        <van-tabbar-item>
          <img src="../../public/img/light.svg" />
          <p>亮度</p>
        </van-tabbar-item>
        <van-tabbar-item>
          <img src="../../public/img/font.svg" @click="showPopup_three" />
          <p>设置</p>
        </van-tabbar-item>
      </van-tabbar>
    </van-popup>

    <!-- 操作层下层的附属层：专门用于控制，字体大小间距等功能 -->
    <van-popup
      v-model="show3"
      position="bottom"
      :style="{ height: '22%' }"
      :get-container="getContainer3"
    >
      <section>
        <div class="control-fs">
          <span>字号</span>
          <ol>
            <li
              v-for="(item, index) in ['超小', '小', '中', '大', '超大']"
              :key="index"
              @click="controlFS(item)"
            >
              {{ item }}
            </li>
          </ol>
        </div>
        <div class="control-bg">
          <span>主题</span>
          <ol>
            <li
              v-for="(item, index) in 5"
              :key="index"
              @click="controlBG(index)"
            ></li>
          </ol>
        </div>
        <div class="control-lh">
          <span>间距</span>
          <ol>
            <li
              v-for="(item, index) in 3"
              :key="index"
              @click="controlLH(index)"
            ></li>
          </ol>
        </div>
      </section>
    </van-popup>

    <!-- 所有章节层 -->
    <van-popup
      v-model="show2"
      position="left"
      :style="{ width: '45%', height: '100%' }"
      :get-container="getContainer2"
    >
      <div class="p-box">
        <div class="contain" v-if="temporaryChapters">
          <p
            class="p-chapters"
            v-for="(item, index) in temporaryChapters"
            :key="index"
          >
            <!-- @click="getTop(index)" -->
            <span
              :class="{ active: index === chaptersIndex }"
              @click="
                updateChaptersIndex(index);
                resetAllChapters(index);
                show2 = false;
              "
            >
              {{ item.title }}
            </span>
          </p>
        </div>
      </div>
    </van-popup>
  </div>
  <div v-else>
    <BeTheBest />
  </div>
</template>

<script>
import { Dialog } from "vant";
import { Toast } from "vant";
import { mapState, mapMutations } from "vuex";
import BeTheBest from "@/components/BeTheBest.vue";
export default {
  components: {
    BeTheBest,
  },
  data() {
    return {
      content: null,
      show: false, //控制popup显示与隐藏
      show2: false, //控制章节显示与隐藏
      show3: false, //控制操作层下层的附属层的显示与隐藏
      active: 0, //控制下面的操作栏
      isNight: false,
      value: 0,
      valueTimer: null,

      // 切页数据
      nowPage: 0,
      // nowPage: JSON.parse(localStorage.getItem("allChapters")).pageNum, //页码
      allPage: null,
      pageTimer: null,
      // 左右滑动
      touchStartX: null,
      touchMoveX: null,
      touching: false,
    };
  },
  computed: {
    ...mapState(["temporaryChapters", "chaptersIndex", "pageNum"]),
    // 当前的章节总页数
    // allPage() {
    //   // console.log(document.querySelector('.read-section'));
    //   // console.log(this.$refs.contentSection);
    //   if (!this.$refs.contentSection) {
    //     return 1;
    //   }
    //   // console.log("father,height", this.$refs.contentSection.offsetHeight);
    //   let sons = this.$refs.contentSection.querySelectorAll("p");
    //   // console.log("sons,height", sons[0].offsetHeight);
    //   let sonCountHeight = 0;
    //   sons.forEach((item) => {
    //     sonCountHeight += item.offsetHeight;
    //   });
    //   // console.log("儿子总高", sonCountHeight);
    //   // console.log(
    //   //   "列数",
    //   //   Math.floor(sonCountHeight / this.$refs.contentSection.offsetHeight)
    //   // );
    //   return Math.floor(
    //     sonCountHeight / this.$refs.contentSection.offsetHeight
    //   );
    // },
  },
  watch: {
    chaptersIndex(n) {
      this.searchchapters();
      this.value = n;
    },
    temporaryChapters() {
      this.searchchapters();
    },
    value(n) {
      if (this.valueTimer) {
        clearTimeout(this.valueTimer);
      }
      Toast("正在切换章节");
      this.valueTimer = setTimeout(() => {
        this.updateChaptersIndex(n);
        this.resetAllChapters(n);
      }, 1000);
    },
    nowPage(n) {
      this.$refs.contentSection.style.transform =
        "translateX(" + -n * (document.body.offsetWidth - 50) + "px)";
    },
  },
  created() {
    this.searchchapters();
    this.value = this.chaptersIndex;
  },
  updated() {
    // if (!this.$refs.contentSection) {
    //   this.allPage = 1;
    // }
    // let sons = this.$refs.contentSection.querySelectorAll("p");
    // let sonCountHeight = 0;
    // sons.forEach((item) => {
    //   sonCountHeight += item.offsetHeight;
    // });
    // this.allPage = Math.floor(
    //   sonCountHeight / this.$refs.contentSection.offsetHeight
    // );
    this.getArticleHeight();
    // 更换主题
    this.useStyle();
  },
  methods: {
    ...mapMutations(["updateBookRack", "updateChaptersIndex"]),
    //查询当前的章节的内容
    searchchapters() {
      this.axios
        .get(
          "http://novel.kele8.cn/chapters/" +
            encodeURIComponent(this.temporaryChapters[this.chaptersIndex].link)
        )
        .then((res) => {
          // console.log(res.data);
          this.content = res.data.chapter;
        })
        .finally(() => {
          // 在查询完章节内容之后再进行章节页码的重新赋值，不然会直接返回到第一页
          this.nowPage = 0;
        });
    },
    //控制popup显示与隐藏
    showPopup() {
      this.show = true;
    },
    //控制章节目录显示与隐藏
    showPopup_two() {
      this.show2 = true;
      this.show = false;
    },
    showPopup_three() {
      this.show = false;
      this.show3 = true;
    },
    // 返回一个特定的 DOM 节点，作为挂载的父节点
    getContainer() {
      return document.querySelector(".my-container");
    },
    getContainer2() {
      return document.querySelector(".my-chapters");
    },
    getContainer3() {
      return document.querySelector(".my-controler");
    },
    // 改变小说的阅读界面的样式
    controlFS(size) {
      let dom = this.$refs.contentSection;
      let num = 16;
      switch (size) {
        case "超小":
          dom.style.fontSize = "10px";
          num = 10;
          break;
        case "小":
          dom.style.fontSize = "14px";
          num = 14;
          break;
        case "大":
          dom.style.fontSize = "20px";
          num = 20;
          break;
        case "超大":
          dom.style.fontSize = "24px";
          num = 24;
          break;
        default:
          dom.style.fontSize = "16px";
          num = 16;
          break;
      }
      this.getArticleHeight();
      this.saveStyle(num, 0);
    },
    controlBG(bg) {
      let bcg = "rgb(210, 180, 140)";
      let dom = document.querySelector(".van-cell");
      if (bg == 5) {
        bg = this.isNight ? 5 : 0;
      } else {
        this.isNight = false;
      }
      switch (bg) {
        case 1:
          dom.style.backgroundColor = "rgb(204, 232, 207)";
          bcg = "rgb(204, 232, 207)";
          break;
        case 2:
          dom.style.backgroundColor = "rgb(247, 238, 229)";
          bcg = "rgb(247, 238, 229)";
          break;
        case 3:
          dom.style.backgroundColor = "rgb(248, 218, 207)";
          bcg = "rgb(248, 218, 207)";
          break;
        case 4:
          dom.style.backgroundColor = "rgb(209, 194, 166)";
          bcg = "rgb(209, 194, 166)";
          break;
        case 5:
          dom.style.backgroundColor = "rgb(0, 0, 0)";
          bcg = "rgb(0, 0, 0)";
          break;
        default:
          dom.style.backgroundColor = "rgb(210, 180, 140)";
          bcg = "rgb(210, 180, 140)";
          break;
      }
      this.fontColor(bg);
      // 设置背景缓存
      this.saveStyle(bcg, 1);
    },
    // 黑色背景的字体颜色逻辑
    fontColor(bg) {
      if (bg == 5) {
        document.querySelector(".van-cell__value--alone").style.color =
          "#d3d3d3";
        document.querySelector("h2").style.color = "#d3d3d3";
      } else {
        document.querySelector(".van-cell__value--alone").style.color = "#000";
        document.querySelector("h2").style.color = "#000";
      }
    },
    controlLH(lh) {
      let dom = this.$refs.contentSection;
      let lh2 = 1.5;
      switch (lh) {
        case 1:
          dom.style.lineHeight = 2.5;
          lh2 = 2.5;
          break;
        case 2:
          dom.style.lineHeight = 3.0;
          lh2 = 3.0;
          break;
        default:
          dom.style.lineHeight = 1.5;
          lh2 = 1.5;
          break;
      }
      this.getArticleHeight();
      this.saveStyle(lh2, 2);
    },
    // 存储样式
    saveStyle(s, n) {
      if (localStorage.getItem("styleModle")) {
        let obj2 = JSON.parse(localStorage.getItem("styleModle"));
        switch (n) {
          case 0:
            obj2.fs = s;
            break;
          case 1:
            obj2.bg = s;
            break;
          case 2:
            obj2.lh = s;
            break;
        }
        window.localStorage.setItem("styleModle", JSON.stringify(obj2));
      } else {
        let obj = { fs: 16, bg: "rgb(210, 180, 140)", lh: 1.5 };
        switch (n) {
          case 0:
            obj.fs = s;
            break;
          case 1:
            obj.bg = s;
            break;
          case 2:
            obj.lh = s;
            break;
        }
        console.log(obj);
        localStorage.setItem("styleModle", JSON.stringify(obj));
      }
    },
    // 用存储的样式
    useStyle() {
      if (!this.$refs.contentSection) {
        return;
      }
      if (localStorage.getItem("styleModle")) {
        let dom = this.$refs.contentSection;
        let obj = JSON.parse(localStorage.getItem("styleModle"));
        dom.style.fontSize = obj.fs + "px";
        dom.style.lineHeight = obj.lh;
        document.querySelector(".van-cell").style.backgroundColor = obj.bg;
        if (obj.bg == "rgb(0, 0, 0)") {
          this.fontColor(5);
        }
      }
    },
    // 上一章
    prevChapter(index) {
      index--;
      index = index > 0 ? index : 0;
      this.value = index;
    },
    // 下一章
    nextChapter(index) {
      index++;
      index =
        index > this.temporaryChapters.length - 1
          ? this.temporaryChapters.length - 1
          : index;
      this.value = index;
    },
    // 重置本地存储的index
    resetAllChapters(index) {
      let ac = JSON.parse(localStorage.getItem("allChapters"));
      ac.index = index;
      localStorage.setItem("allChapters", JSON.stringify(ac));
    },
    // 回到详情页页面和添加书架逻辑
    Back() {
      // 路由离开时设置是否加入书架;
      if (window.localStorage.getItem("myBookRack")) {
        // 如果有本地存储的书架数据则判断是否拥有此书本
        // console.log(this.$route.params.id);
        let mybookArr = [...JSON.parse(localStorage.getItem("myBookRack"))];
        // 获取当前的书本位于书架的下标
        let bookpos = mybookArr.findIndex((item) => {
          return item.id === this.$route.params.id;
        });
        if (bookpos !== -1) {
          // 如果书架的书本在离开的时候已经读取过n章，就需要重新赋值index值
          mybookArr[bookpos].index = this.chaptersIndex;
          // 书籍偶尔会进行更新，所以也需要进行目录的重新赋值
          mybookArr[bookpos].chapter = this.temporaryChapters;
          mybookArr[bookpos].expire = Date.now() + 24 * 3600 * 1000;
          // mybookArr[bookpos].pageNum = this.nowPage; //页码
          localStorage.setItem("myBookRack", JSON.stringify([...mybookArr]));
          console.log("匹配到位，存在");
          this.updateBookRack();
          this.$router.back();
        } else {
          // 如果书本不存在与书架中则执行是否添加书本逻辑
          Dialog.confirm({
            title: "温馨提示",
            message: "是否要添加入书架！！！",
          })
            .then(() => {
              // 书籍id和书籍源id和index值和当前的阅读章节下标
              let obj = {
                id: this.$route.params.id,
                index: this.chaptersIndex,
                chapter: this.temporaryChapters,
                expire: Date.now() + 24 * 3600 * 1000, //设置时效
                // pageNum: this.nowPage, //页码
              };
              localStorage.setItem(
                "myBookRack",
                JSON.stringify([...mybookArr, obj])
              );
              Toast("加入书架成功 o(￣▽￣)ｄ");
              this.updateBookRack();
              this.$router.back();
            })
            .catch(() => {
              // on cancel
              console.log('你按下了"取消"按钮!');
              this.$router.back();
            });
        }
      } else {
        // 如果没有本地存储的书架数据则执行是否添加本书籍
        Dialog.confirm({
          title: "温馨提示",
          message: "是否要添加入书架！！！",
        })
          .then(() => {
            let obj = {
              id: this.$route.params.id,
              index: this.chaptersIndex,
              chapter: this.temporaryChapters,
              expire: Date.now() + 3600 * 1000, //设置时效
              // pageNum: this.nowPage, //页码
            };
            localStorage.setItem("myBookRack", JSON.stringify([obj]));
            this.updateBookRack();
            Toast("加入书架成功 o(￣▽￣)ｄ");
            this.$router.back();
          })
          .catch(() => {
            // on cancel
            console.log('你按下了"取消"按钮!');
            this.$router.back();
          });
      }
    },
    // ***获取当前的article的高度
    getArticleHeight() {
      if (!this.$refs.contentSection) {
        this.allPage = 1;
        return;
      }
      let sons = this.$refs.contentSection.querySelectorAll("p");
      let sonCountHeight = 0;
      sons.forEach((item) => {
        sonCountHeight += item.offsetHeight;
      });

      this.allPage = Math.floor(
        sonCountHeight / this.$refs.contentSection.offsetHeight
      );
      // 在切换字体大小、行间大小的时候会出现当前页码比总页码大，所以需要调整
      if (this.nowPage >= this.allPage) {
        this.nowPage = this.allPage;
      }
    },
    readtouchstart(e) {
      // console.log("touchstart", e.targetTouches[0].pageX);
      this.touchStartX = e.targetTouches[0].pageX;
      this.touching = true;
    },
    readtouchmove(e) {
      if (this.touching) {
        // console.log("touchmove", e.targetTouches[0].pageX);
        this.touchMoveX = e.targetTouches[0].pageX;
      }
    },
    readtouchend() {
      // 如果单单只是点击了，那么就禁止这个readtouchend函数的继续运行
      if (!this.touchMoveX) {
        this.touchStartX = null;
        this.touchMoveX = null;
      }
      // console.log(this.touchStartX, this.touchMoveX);
      this.touching = false;
      if (this.touchStartX < this.touchMoveX - 50) {
        // console.log("右滑");
        this.nowPage--;
        if (this.nowPage < 0) {
          this.nowPage = 0;
          if (this.chaptersIndex > 0) {
            // 设置延时器是为了防止一次性多切几个章节
            if (this.pageTimer) {
              clearTimeout(this.pageTimer);
            }
            this.pageTimer = setTimeout(() => {
              this.value--;
              clearTimeout(this.pageTimer);
            }, 500);
            if (this.value < 0) {
              this.nowPage = 0;
              return;
            }
            this.nowPage = 0;
          }
        }
      }
      if (this.touchMoveX < this.touchStartX - 50) {
        // console.log("左滑");
        this.nowPage++;
        if (this.nowPage > this.allPage) {
          // 设置延时器是为了防止一次性多切几个章节
          if (this.pageTimer) {
            clearTimeout(this.pageTimer);
          }
          this.pageTimer = setTimeout(() => {
            this.value++;
            clearTimeout(this.pageTimer);
          }, 500);

          if (this.value > this.temporaryChapters.length - 1) {
            this.nowPage = this.allPage;
            return;
          }
          this.nowPage = this.allPage;
        }
      }
      // console.log("页数", this.nowPage, this.allPage);
      this.touchStartX = null;
      this.touchMoveX = null;
    },
  },
};
</script>

<style lang="less">
.read {
  .my-container {
    .van-overlay {
      background: transparent;
    }
    .van-popup--top {
      .van-nav-bar {
        height: 100%;
        line-height: 40px;
        .van-nav-bar__content {
          height: 40px;
          .van-icon-arrow-left {
            color: #000;
          }
        }
      }
    }
    .van-popup--bottom {
      .controlChapter {
        margin-top: 20px;
        height: 50px;
        display: flex;
        align-items: center;
        span {
          display: block;
          width: 80px;
          height: 25px;
          font-size: 12px;
          line-height: 25px;
          text-align: center;
          border: 1px solid transparent;
          border-radius: 10px;
          margin: 0 10px;
          &:active {
            border: 1px solid #ccc;
            background: #ccc;
          }
        }
      }
      .van-hairline--top-bottom {
        .van-tabbar-item--active {
          color: #646566;
        }
        &::after {
          border: none;
        }
        // height: 100px;
        margin-bottom: 5px;
        .van-tabbar-item {
          .van-tabbar-item__text {
            text-align: center;
            img {
              width: 20px;
              height: 20px;
              margin-bottom: 3px;
            }
          }
        }
      }
    }
  }
  .my-controler {
    .van-overlay {
      background: transparent;
    }
    section {
      padding: 10px 15px;
      box-sizing: border-box;
      div {
        width: 100%;
        height: 40px;
        display: flex;
        align-items: center;
        font-size: 12px;
        span {
          margin-right: 10px;
        }
        ol {
          li {
            width: 40px;
            height: 22px;
            float: left;
            margin-right: 10px;
            border: 1px solid transparent;
            border-radius: 12px;
            box-sizing: border-box;
          }
        }
      }
      .control-fs {
        li {
          border-color: #999;
          text-align: center;
          padding: 2px 5px;
        }
      }
      .control-bg {
        li {
          background: rgb(210, 180, 140);
          &:nth-of-type(2) {
            background: rgb(204, 232, 207);
          }
          &:nth-of-type(3) {
            background: rgb(247, 238, 229);
          }
          &:nth-of-type(4) {
            background: rgb(248, 218, 207);
          }
          &:nth-of-type(5) {
            background: rgb(209, 194, 166);
          }
        }
      }
      .control-lh {
        li {
          border-color: #666;
          width: 80px;
          height: 26px;
          background-size: 20px 20px;
          background-position: center;
